import { Carousel } from "react-bootstrap";
import classes from './Slider.module.css'
import { Fragment} from "react";
import NullImage from "../Imges/unnamed.png"
// import FetchProducts from "../store/FetchProducts";

function ControlledCarousel() {

  // Fetch.fetchProducts()


  
const image1 =  NullImage
const image2 = NullImage
const image3 = NullImage
const image4 = NullImage





    return (
      <Fragment>         
           <div className={classes.place}>
           <Carousel  className={classes.slider} >
           <Carousel.Item>
           <img
            className="d-block w-100"
            src={image1? image1:'null'}
            alt="First slide"
           />
           </Carousel.Item>
           <Carousel.Item>
           <img
            className="d-block w-100"
            src={image2?image2:'null'}
            alt="Second slide"
          />
          </Carousel.Item>
          <Carousel.Item>
          <img 
            className="d-block w-80"
            src={image3?image3:'null'}
            alt="Third slide"
          />
          </Carousel.Item>
          </Carousel>
           <Carousel  className={classes.slider} >
           <Carousel.Item>
           <img
            className="d-block w-100"
            src={image4? image4:'null'}
            alt="First slide"
           />
           </Carousel.Item>
           <Carousel.Item>
           <img
            className="d-block w-100"
            src={image2?image2:'null'}
            alt="Second slide"
          />
          </Carousel.Item>
          <Carousel.Item>
          <img 
            className="d-block w-80"
            src={image3?image3:'null'}
            alt="Third slide"
          />
          </Carousel.Item>
          </Carousel>
           <Carousel  className={classes.slider} >
           <Carousel.Item>
           <img
            className="d-block w-100"
            src={image1? image1:'null'}
            alt="First slide"
           />
           </Carousel.Item>
           <Carousel.Item>
           <img
            className="d-block w-100"
            src={image2?image2:'null'}
            alt="Second slide"
          />
          </Carousel.Item>
          <Carousel.Item>
          <img 
            className="d-block w-80"
            src={image3?image3:'null'}
            alt="Third slide"
          />
          </Carousel.Item>
          </Carousel>
          </div>
          </Fragment>

    );
  }
  
export default ControlledCarousel