import React, { useContext, useRef, useState } from 'react'
import {  Form,  Button, Card, Alert,} from 'react-bootstrap'
import {  useHistory } from 'react-router'
import { Link } from 'react-router-dom'
import AuthContext from '../store/auth-context'
import classes from './SignUp.module.css'


const SignUp = () => {
  const emailInputRef = useRef()
  const passwordInputRef = useRef()
  const confirmPasswordInputRef = useRef()
  const phoneInputRef = useRef()
  const addressInputRef = useRef()
  const cityInputRef = useRef()
  const authCtx = useContext(AuthContext)
  const [isLoggin, setIsLoggin] = useState(true)
  const [error, seterror] = useState('')
  const [success, setsuccess] = useState('')
  const [isLoading, setIsLoading] = useState(false);
  // const [isTouched, setIsTouched] = useState(true)
  const [confirmPassword, setconfirmPassword] = useState(true)
  const history =   useHistory()

  const onFormSubmit = (e) => {
  setIsLoggin(true)
  e.preventDefault()

const emailEntered = emailInputRef.current.value
const passwordEntered = passwordInputRef.current.value
const ConfirmpasswordEntered = confirmPasswordInputRef.current.value
const phoneEntered = phoneInputRef.current.value
const addressEntered = addressInputRef.current.value
const cityEntered = addressInputRef.current.value


if (ConfirmpasswordEntered !== passwordEntered) {

 return  setconfirmPassword(false)

}

fetch('https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyAWnOKCBGTPaXpK7nx6cM4ELazrACRBGyM',
{
    method: 'POST',
    body:JSON.stringify({
        email: emailEntered,
        password: passwordEntered,
        Confirmpassword:ConfirmpasswordEntered,
        returnSecureToken:true,
        address:addressEntered,
        phone:phoneEntered,
        city:cityEntered
    }),
    headers:{
        'Content-Type':'application/json'
    }
}
).then((res)=>{
  if (res.ok) {
    setsuccess(<h4>Success!</h4>);
    setIsLoading(true);
   res.json().then((data)=> authCtx.login(data.idToken))
   history.replace('/Shopping-Cart/' )
    }else{
      return   res.json().then((data)=>{
        console.log(data.error.message);   
        setIsLoggin(false);
        seterror(data.error.message)
      })
    }
  })

 
 
}




    return (
      <div>
         {/* {isLoading &&  <Redirect to='/Shopping-Cart/' />} */}
        <Card className={classes.SignUp}>
        <Alert show={!isLoggin} variant="danger">
        <Alert.Heading>ERROR: {error}</Alert.Heading>
        </Alert>
        <Alert show={isLoading} variant="primary">
        <Alert.Heading>{success}</Alert.Heading>
        </Alert>
        <Alert show={!confirmPassword} variant="danger">
        <Alert.Heading>Confirm Password Not Is Valid!ðŸ˜«</Alert.Heading>
        </Alert>
        <Form onSubmit={onFormSubmit}>
        <Form.Group className="mb-3" controlId="formBasicEmail" >
        <Form.Label>Email address</Form.Label>
        <Form.Control type="email" placeholder="Enter email" required  ref={emailInputRef}/>
        <Form.Text className="text-muted">
        We'll never share your email with anyone else.
       </Form.Text>
       </Form.Group>

     <Form.Group className="mb-3" controlId="formBasicPassword">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Password" required ref={passwordInputRef}
maxLength="25" minLength="6"
    />
  </Form.Group>
  <Form.Group className="mb-3" controlId="formBasicPassword">
    <Form.Label>Confirm Password</Form.Label>
    <Form.Control type="password" placeholder="Password" required ref={confirmPasswordInputRef}
    maxLength="25" minLength="6"
    />
  </Form.Group>
  <Form.Group className="mb-3" >
    <Form.Label htmlFor="phone">Phone Number</Form.Label>
    <Form.Control type="tel" placeholder="Phone Number" required  
    ref={phoneInputRef}
    />
  </Form.Group>
  <Form.Group className="mb-3" >
  <Form.Label>City</Form.Label>
              <Form.Control
                type="text"
                placeholder="City"
                name="city"
                ref={cityInputRef}
              />
        </Form.Group>
     <Form.Group className="mb-3" >
     <Form.Label>Address</Form.Label>
              <Form.Control
                type="address"
                placeholder="Address"
                name="address"
                ref={addressInputRef}
              />
        </Form.Group>
  <Form.Group className="mb-3" controlId="formBasicCheckbox">
    <Form.Check type="checkbox" label="Check me out" />
  </Form.Group>
  {<Button variant="primary" type="submit">
    Submit
  </Button>}

</Form>
<br/>
<h4>Or</h4>
<Link to='/Login/'><span>Already Have Account
      </span>
         </Link>
        </Card>
        </div>
    )
}

export default SignUp
