
import { useCart } from 'react-use-cart';
import CartItems from './CartItems';

const Cart =()=>{
  const {
    isEmpty,
    items,
  } = useCart();
  
  if (isEmpty) {
    return <h2> DashBoard Is Empty</h2>
  }  
  



const CartItem = items.map(cart => (
  <CartItems
    key={cart.id}
    id={cart.id}
    Name={cart.Name}
    Price={cart.price}
    Image={cart.thumbnail}
    Amount={cart.rating.average}
    />))
    
  
  return (
      <div>  
     {items.length === 0 &&!items && <h2>DashBoard Is Empty</h2>}
        {!CartItem && <p>LoadCartItems</p>} 
        {CartItem}
   </div>

    )
};

export default Cart;