import classes from './bookmark.module.css'
import { Fragment} from 'react';
import { removeBookmark } from '../store/bookmark-hook';
import { Button } from 'react-bootstrap';


const BookMarkItems =(props)=>{
// const getItem = JSON.parse(localStorage.getItem('itemsTotal'))

const removeBook= ()=>{
  removeBookmark(props)
  window.location.reload(false);
}

return (
  <Fragment > 
          <div className={classes.products}>
          <img  src={props.Image}  alt="Images" style={{'width':'100%'}} />
          <div className={classes.container}>
          <p >{props.Name}</p>
          <h4 >Rating:<b>{props.Price}</b></h4>
          <Button onClick={removeBook}>&times;</Button>
          </div>
          </div>
         </Fragment>

    )
};

export default BookMarkItems;