
    const getItem  = JSON.parse(localStorage.getItem('book'))
     
    let updatedItems=[]; 
    localStorage.setItem('book',JSON.stringify(getItem?getItem:[]));
    export const addBookmark = (meal) => {
    const getItem  = JSON.parse(localStorage.getItem('book'))
    updatedItems.push(meal);
    localStorage.setItem('book',JSON.stringify(updatedItems));
    if(!getItem) {return false}
    if(updatedItems.filter((item)=> item.Name === meal.Name)){
    let newItems = [];
    localStorage.setItem('book',JSON.stringify(newItems.concat(getItem,meal)));
     }
}
    export const removeBookmark = (meals) =>{
    const getItem  = JSON.parse(localStorage.getItem('book'))
    const deleteItem = (getItem||[]).filter((item)=> item.Name !== meals.Name);
    localStorage.setItem('book',JSON.stringify(deleteItem));
   
}
   