import React, {  useState } from 'react';
import { Fragment } from 'react';
import Products from './Products';
import classes from './searchBar.module.css'
import images from '../Imges/CS-GO-image.jpg'
import Pagination  from '../Post/Pagination' 
import FetchProducts from '../store/FetchProducts'




const SearchBar =()=>{
      const Fetch = FetchProducts()
   console.log(Fetch.products)
      const [currentPage,setCurrentPage] = useState(1)

      const resultPerPage = 20

      const indexOfFirstPost =( currentPage -1 ) * resultPerPage;
      const indexOfLastPost = currentPage * resultPerPage;
      const currentPosts = Fetch.products.slice(indexOfFirstPost, indexOfLastPost)
      const nextPage = (number) => setCurrentPage(currentPage + number)

  


     const productsList =  currentPosts.map((meal) => {

    return(
    <Products
    id={meal.id}
    key={meal.id }
    Name={meal.name}
    airdate={meal.premiered}
    EndDate={meal.ended}
    genres={meal.genres}
    thumbnail={meal.image?meal.image.original :images}
    summary={meal.summary}
    price={meal.rating.average}
    runtime={meal.runtime}
    url={meal.url}
    rating={meal.rating.average}
      />
      )
 })

 
 const SeachValue = (props) => {
   props.preventDefault()
   Fetch.setIsProducts(props.target.value)
   
  }
  
  const onKeyDown = ev => {
    if (ev.keyCode === 13) {
      Fetch.fetchProducts()
      setCurrentPage(1)
    }
  };


 return(
   <Fragment>
    <div className={classes.search}>
    <input 
    value={Fetch.isProducts} 
    onChange={SeachValue} 
    placeholder="Search.."
    type="text"
    id="search"
    onKeyDown={onKeyDown}
     />
    <button  onClick={Fetch.fetchProducts} >Search</button>
    </div><br/><br/>
    {!Fetch.touched && <h2 className={classes.product}>Search The Best.............</h2>}
    {Fetch.products && <Pagination  
    products={Fetch.products}
    resultPerPage={resultPerPage} 
    currentPage={currentPage}
    nextPage={nextPage}
    />}<br/>
    {Fetch.touched && productsList.length===0 && <h2 className={classes.product}>No products Found...</h2>}
    {Fetch.touched && productsList}
    {Fetch.products && <Pagination 
     products={Fetch.products}
     resultPerPage={resultPerPage} 
     currentPage={currentPage}
     nextPage={nextPage}
    />}
    </Fragment>
 )   
}

export default SearchBar;