import React, { useContext } from 'react';
import { BsBucketFill ,BsBucket} from 'react-icons/bs';
import {Link} from 'react-router-dom'
import classes from './Header.module.css'
import { useCart } from 'react-use-cart';
import { useMediaQuery } from 'react-responsive';
import { Button, Card, Dropdown } from 'react-bootstrap';
import AuthContext from '../store/auth-context';

const Header =()=>{
const {  totalUniqueItems} = useCart();
const cartStatus = totalUniqueItems === 0|| !totalUniqueItems
const authCtx = useContext(AuthContext)
const isLoggedIn = authCtx.isLoggedIn

const LogOut = () =>{
  authCtx.logout()
}

  if ( useMediaQuery({ query: '(max-width: 640px)' })) {
   
   return( 
     <div>
       <Card style={{width:'100%' ,alignItems:'center' , backgroundColor:"goldenrod"}}>
         <h3 >Shopping</h3>
         <h3 >Cart</h3>
   <Dropdown className="mt-1 mb-1">
  <Dropdown.Toggle variant="success" id="dropdown-basic">
   Drop
  </Dropdown.Toggle>

  <Dropdown.Menu>
    {isLoggedIn&&<Dropdown.Item >
    <Link to='/Shopping-Cart/CartItems'><span>DashBoard
        {!cartStatus?<BsBucketFill />:<BsBucket/>}
       {totalUniqueItems?totalUniqueItems:'0'}</span></Link> 
    </Dropdown.Item>}
   {isLoggedIn && <Dropdown.Item>
    <Link to='/Shopping-Cart/BookmarkItems'><span>BookMarkitems
      </span>
         </Link>  
    </Dropdown.Item>}
  {!isLoggedIn && <Dropdown.Item >
    <Link to='/Signup/'><span>SignUp
      </span>
         </Link>  
    </Dropdown.Item>}
   {!isLoggedIn && <Dropdown.Item >
    <Link to='/Login/'><span>LogIn
      </span>
         </Link>  
    </Dropdown.Item>}
    {<Dropdown.Item >
    <Link to='/Shopping-Cart'><span>HOME</span> </Link>  
    </Dropdown.Item>}
    { isLoggedIn && <Dropdown.Item >
      <Button onClick={LogOut}><span>LogOut</span></Button>  
    </Dropdown.Item>}
  </Dropdown.Menu>
</Dropdown>
</Card>
</div>
)
  }
 return(
    <div className={classes.header} >
    <ul>
    <li>
    <h2 className={classes.heading}>Shopping.Com </h2>
    </li>
 {isLoggedIn &&   <li>
    <h3>
      <Link to='/Shopping-Cart/CartItems'><span>DashBoard
        {!cartStatus?<BsBucketFill />:<BsBucket/>}
       {totalUniqueItems?totalUniqueItems:'0'}</span></Link>  
    </h3>
    </li>}
 {isLoggedIn &&<li>
    <h3>
      <Link to='/Shopping-Cart/BookmarkItems'><span>BookMarkitems
      </span>
         </Link>  
    </h3>
    </li>}
  {!isLoggedIn &&  <li>
    <h3>
      <Link to='/Signup/'><span>SignUp
      </span>
         </Link>  
    </h3>
    </li>}
   {!isLoggedIn &&<li>
    <h3>
      <Link to='/Login/'><span>LogIn
      </span>
         </Link>  
    </h3>
    </li>}
    <li>
    <h3>
      <Link to='/Shopping-Cart'><span>HOME</span> </Link>  
    </h3>
    </li>
   {isLoggedIn && <li>
    <h3>
      <Button onClick={LogOut}><span>LogOut</span></Button>  
    </h3>
    </li>}
  </ul>
</div>
)

}
export default Header;