import React, { useState } from 'react'

    const FetchProducts = () => {

    const [isProducts, setIsProducts] = useState('')
    const [products, setProducts] = useState([])
    const [render, setRender] = useState([])
    const [touched, settouched] = useState(false)

    
    const fetchProducts = async () => {
    const response = await fetch(` https://api.tvmaze.com/shows`)
    const responseData = await response.json();
    const filteredProducts = responseData.filter((user) => user.name.toLowerCase().trim().includes(isProducts.toLowerCase().trim()))
      
    setRender(responseData.slice(0 ,20))
    
    if (isProducts.length === 0 && !isProducts) {
        return <p>Bla Bla</p>
    }
    
    setProducts(filteredProducts)
    settouched(true)
    }

    return {fetchProducts , products, setIsProducts ,touched,isProducts ,render}        
    }

    export default FetchProducts
