import React, { useContext } from 'react';
import Cart from './Cart/Cart'
import {  Redirect ,Route, Switch } from 'react-router-dom';
import Header from './Header/Header';
import SearchBar from './SearchBar/searchbar';
import Products from './SearchBar/Products';
import Info from './Information/info';
import { CartProvider } from "react-use-cart";
import 'bootstrap/dist/css/bootstrap.min.css'
import BookMark from './bookmark/bookmark';
import SignUp from './Auth/SignUp';
import LogIn from './Auth/LogIn';
import AuthContext from './store/auth-context';
import Slider from './Slider/Slider';



function App() {
  const authCtx = useContext(AuthContext)
  const isLoggedIn = authCtx.isLoggedIn
  return (
<CartProvider>
<div className="App">
{/* <Button /> */}
<Header />
<Slider />
<Switch>
{isLoggedIn && <Route path='/Shopping-Cart/BookmarkItems' exact >
<BookMark/>
</Route>}
<Route path='/' exact>
<Redirect to='/Shopping-Cart/' />
</Route>

<Route path='/Shopping-Cart/' exact>
<SearchBar/>
</Route>

{isLoggedIn &&<Route path='/Shopping-Cart/:CartItems' exact >
<Cart/>
</Route>}
{<Route path='/CartItems/:productId'exact>
<Products/>
</Route>}
<Route path='/Shopping-Cart/Info/:id' exact> 
<Info/>
</Route>
{!isLoggedIn && <Route path='/Signup/' exact>
<SignUp/>
</Route>}
{!isLoggedIn && <Route path='/Login/'exact>
<LogIn/>
</Route>}
<Route path="*">
  <Redirect to='/Shopping-Cart/'/>
</Route>
<Route>
<div>Not Found 404</div>
</Route>
</Switch>
</div>
</CartProvider>
  );
}


export default App;
